
body {
    background-image: url(/text_editor/static/media/img3rt.4108876c.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    min-height: 100vh;
    height: 100%;
}

#font{
    color: rgb(55, 23, 69);
}
.lines{
    background-image: linear-gradient(to right top, #eff5d6, #e4f7dc, #daf7e3, #d3f7ec, #d0f6f3, #d3f6f9, #d9f6fd, #dff5ff, #eaf6ff, #f3f8ff, #fafbff, #fefefe);
}

.scrl{
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none; 
    height: 10em;
    border-radius: 7px
}
.scrl::-webkit-scrollbar {
    display: none;
}
.textarea{
  background-image: linear-gradient(to right top, #eff5d6, #e4f7dc, #daf7e3, #d3f7ec, #d0f6f3, #d3f6f9, #d9f6fd, #dff5ff, #eaf6ff, #f3f8ff, #fafbff, #fefefe);
 }
#heading{
  margin-top: 1%;
  background-image: linear-gradient(to right top, #cc84db, #ff79b6, #ff7f83, #ff9a50, #e9bc29);  width: -webkit-fit-content;  width: -moz-fit-content;  width: fit-content;
  padding: .4% .6%;
  border-radius: 7px;
  font-family: Arial, Helvetica, sans-serif;
  border-style:solid;
  color: #58ecc7;
  border-color: #140ebf2d;
  text-shadow: #7211f0;
  
}
#fb{
  display: flex;
  justify-content: center;
  align-items: center;

}


